<template>
  <div class="logo">
    <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" id="content">
      <h3 class="title">后台登录</h3>
      <el-form-item prop="name">
        <el-input type="text" v-model="ruleForm2.name" auto-complete="off" placeholder="账号"></el-input>
      </el-form-item>
      <el-form-item prop="pwd">
        <el-input type="password" v-model="ruleForm2.pwd" auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <p class="point">{{point}}</p>
      <el-form-item style="width:100%;">
        <el-button style="width:100%;" @click="login">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import qs from "qs";
export default {
  data() {
    return {
      point: "",
      ruleForm2: {
        name: "admin",
        pwd: "123456"
      },
      rules2: {
        name: [
          { required: true, message: "请输入账号", trigger: "blur" }
          // { validator: validaePass }
        ],
        pwd: [
          { required: true, message: "请输入密码", trigger: "blur" }
          // { validator: validaePass2 }
        ]
      }
    };
  },
  methods: {
    login() {
      let newData = qs.stringify(this.ruleForm2);
      console.log(newData);
      this.$http.post("carrots-admin-ajax/a/login", newData).then(res => {
        console.log(res);
        if (res.data.code != 0) {
          this.point = "无此用户！";
        } else {
          this.$router.push({ path: "/HelloWorld" });
        }
      });
    }
  }
};
</script>

<style>
body {
  margin: auto;
}
.logo {
  background: url(../assets/logo.jpg) no-repeat center/cover;
  background-size: 100% 100%;
  height: 100%;
  position: fixed;
  width: 100%;
}
#content {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.point {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  color: #f56c6c;
  margin: 0 0 20px;
  font-size: 12px;
}
</style>



