<template>
  <div class="content">
    <div class="headers">{{headers}}</div>
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="标题名称" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="类型" prop="types">
        <el-select class="el-select1" v-model="ruleForm.types" placeholder="请选择类型">
          <el-option label="首页banner" value="0"></el-option>
          <el-option label="找职位banner" value="1"></el-option>
          <el-option label="找精英banner" value="2"></el-option>
          <el-option label="行业大图" value="3"></el-option>
        </el-select>
        <el-select
          class="el-select1"
          v-model="ruleForm.region"
          placeholder="请选择类型"
          v-if="this.ruleForm.types == 3"
        >
          <el-option label="移动互联网" value="0"></el-option>
          <el-option label="电子商务" value="1"></el-option>
          <el-option label="企业服务" value="2"></el-option>
          <el-option label="O2O" value="3"></el-option>
          <el-option label="教育" value="4"></el-option>
          <el-option label="金融" value="5"></el-option>
          <el-option label="游戏" value="6"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="说明" prop="desc">
        <div class="edit_container">
          <quill-editor
            v-model="ruleForm.desc"
            ref="myQuillEditor"
            :options="editorOption"
            @blur="onEditorBlur($event)"
            @focus="onEditorFocus($event)"
            @change="onEditorChange($event)"
          ></quill-editor>
        </div>
      </el-form-item>
      <el-form-item label="跳转链接" prop="link">
        <el-input type="text" v-model="ruleForm.link" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="配图" prop>
        <el-upload
          style="text-align: left;"
          class="upload-demo"
          ref="upload"
          action="carrots-admin-ajax/a/u/img/task"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :on-change="fileChange"
          :on-exceed="handleExceed"
          :file-list="fileList"
          :auto-upload="false"
          :limit="1"
          submit
        >
          <el-button slot="trigger" size="small" type="primary">选取文件</el-button>

          <p>
            <img :src="url" alt="图片预览" id="url" class="imge" />
          </p>
        </el-upload>
        <div class="Layout-list" style="margin-left: 0">
          <div>
            <div>图片名</div>
            <div v-if="fileList != '' ">{{ this.fileList[0].name }}</div>
          </div>
          <div>
            <div>文件大小</div>
            <div v-if="fileList != '' ">{{ (this.fileList[0].size / 1024000).toFixed(2) + "MB"}}</div>
          </div>
          <div>
            <div>进度</div>
            <div class="clip" :style="[width]"></div>
          </div>
          <div>
            <div>操作</div>
            <div v-if="success != '' ">
              <i class="el-icon-check"></i>
            </div>
          </div>
          <div>
            <div>操作</div>
            <div v-if="fileList != '' ">
              <el-button
                size="small"
                type="success"
                @click="submitUpload"
                style="background: #5cb85c;border-color: #4cae4c"
              >上传</el-button>
              <el-button
                size="small"
                type="success"
                @click="remove()"
                style="background: #d9534f;border-color: #d43f3a;"
              >删除</el-button>
            </div>
          </div>
        </div>
      </el-form-item>
      <el-form-item>
        <el-button
          style="float: left;background: #5cb85c;border-color: #4cae4c"
          type="primary"
          @click="online('ruleForm')"
          :disabled="this.url == '' || this.ruleForm.name == '' || this.ruleForm.types == '' || this.ruleForm.desc == '' || this.ruleForm.link == '' "
        >立即上线</el-button>
        <el-button
          style="float: left;background: #5cb85c;border-color: #4cae4c"
          type="primary"
          @click="draft('ruleForm')"
          :disabled="this.url == '' || this.ruleForm.name == '' || this.ruleForm.types == '' || this.ruleForm.desc == '' || this.ruleForm.link == '' "
        >存为草稿</el-button>
        <el-button @click="cancel" style="float: right;">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import qs from "qs";
import axios from "axios";
import { quillEditor } from "vue-quill-editor"; //调用编辑器
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
export default {
  //进入页面执行
  created() {
    this.createds();
  },
  components: {
    quillEditor
  },
  data() {
    return {
      headers: "新增Article",
      success: "",
      editorOption: {},
      fileList: [],
      url: "",
      //文件名，文件大小
      filename: "",
      filesize: "",
      //进度条
      num: 0,
      width: {
        width: "0px",
        height: "10px"
      },
      ruleForm: {
        name: "",
        types: "",
        desc: "",
        link: "",
        region: ""
      },
      rules: {
        name: [
          { required: true, message: "请输入标题名称", trigger: "blur" },
          { min: 1, max: 6, message: "长度在 1 到 6 个字符", trigger: "blur" }
        ],
        types: [{ required: true, message: "请选择类型", trigger: "change" }],
        desc: [{ required: true, message: "请补充说明", trigger: "blur" }],
        link: [
          { required: true, message: "请输入跳转链接", trigger: "blur" },
          {
            pattern: /^(?=^.{3,255}$)(http(s)?:\/\/)?(www\.)?[a-zA-Z0-9][-a-zA-Z0-9]{0,62}(\.[a-zA-Z0-9][-a-zA-Z0-9]{0,62})+(:\d+)*(\/\w+\.\w+)*$/,
            message: "请输入正确的网址"
          }
        ]
        // matching: [{ required: true, message: "请配图", trigger: "blur" }]
      }
    };
  },
  methods: {
    //编辑获取id
    createds() {
      if (this.$route.params.type == 1) {
        let _this = this;
        _this.headers = "编辑Article";
        var EditID = localStorage.getItem("EditID");
        console.log(EditID);
        axios
          .get("carrots-admin-ajax/a/article/" + EditID)
          .then(function(res) {
            console.log(res.data.data.article);
            _this.ruleForm.name = res.data.data.article.title;
            _this.ruleForm.types = JSON.stringify(res.data.data.article.type);
            _this.ruleForm.desc = res.data.data.article.content;
            _this.ruleForm.link = res.data.data.article.url;
            _this.url = res.data.data.article.img;
            _this.ruleForm.region = JSON.stringify(
              res.data.data.article.industry
            );
          })
          .catch(function(err) {
            console.log(err);
          });
      }
    },
    //立即上线功能
    online(formName) {
      if (this.$route.params.type == 1) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            console.log(this.ruleForm.region);
            let data = {
              title: this.ruleForm.name,
              type: this.ruleForm.types,
              content: this.ruleForm.desc,
              url: this.ruleForm.link,
              status: "2",
              img: this.url,
              industry: JSON.parse(this.ruleForm.region),
              createAt: Date.parse(new Date())
            };
            let newData = qs.stringify(data);
            console.log(data);
            var EditID = localStorage.getItem("EditID");
            axios
              .put("carrots-admin-ajax/a/u/article/" + EditID, newData)
              .then(res => {
                console.log(res);
                this.$router.push({ path: "/table" });
                this.$alert("编辑成功！", "提示", {
                  confirmButtonText: "确定"
                });
              });
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      } else {
        this.$refs[formName].validate(valid => {
          if (valid) {
            console.log(this.ruleForm);
            let data = {
              title: this.ruleForm.name,
              type: this.ruleForm.types,
              content: this.ruleForm.desc,
              url: this.ruleForm.link,
              status: "2",
              img: this.url,
              industry: this.ruleForm.region
            };
            let newData = qs.stringify(data);
            console.log(data);
            axios.post("carrots-admin-ajax/a/u/article", newData).then(res => {
              console.log(res);
              this.$router.push({ path: "/table" });
              this.$alert("新增成功！", "提示", {
                confirmButtonText: "确定"
              });
            });
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      }
    },
    //存为草稿按钮
    draft(formName) {
      if (this.$route.params.type == 1) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            console.log(this.ruleForm.region);
            let data = {
              title: this.ruleForm.name,
              type: this.ruleForm.types,
              content: this.ruleForm.desc,
              url: this.ruleForm.link,
              status: "1",
              img: this.url,
              industry: JSON.parse(this.ruleForm.region),
              createAt: Date.parse(new Date())
            };
            let newData = qs.stringify(data);
            console.log(data);
            var EditID = localStorage.getItem("EditID");
            axios
              .put("carrots-admin-ajax/a/u/article/" + EditID, newData)
              .then(res => {
                console.log(res);
                this.$router.push({ path: "/table" });
                this.$alert("编辑成功！", "提示", {
                  confirmButtonText: "确定"
                });
              });
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      } else {
        this.$refs[formName].validate(valid => {
          if (valid) {
            console.log(this.ruleForm);
            let data = {
              title: this.ruleForm.name,
              type: this.ruleForm.types,
              content: this.ruleForm.desc,
              url: this.ruleForm.link,
              status: "1",
              img: this.url,
              industry: this.ruleForm.region
            };
            let newData = qs.stringify(data);
            console.log(data);
            axios.post("carrots-admin-ajax/a/u/article", newData).then(res => {
              console.log(res);
              this.$router.push({ path: "/table" });
              this.$alert("新增成功！", "提示", {
                confirmButtonText: "确定"
              });
            });
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      }
    },
    onEditorReady(editor) {
      // 准备编辑器
    },
    onEditorBlur() {}, // 失去焦点事件
    onEditorFocus() {}, // 获得焦点事件
    onEditorChange() {}, // 内容改变事件
    fileChange(file) {
      this.fileList.push(file.raw);
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      delete fileList[0];
    },
    //上传按钮
    submitUpload() {
      // 进度条
      for (var i = 0; i < 90; i++) {
        this.num = i;
      }
      this.width = {
        width: this.num + "px"
      };
      var files = new FormData();
      files.append("file", this.fileList[0]);
      console.log(this.fileList[0]);
      axios
        .post("carrots-admin-ajax/a/u/img/task", files, {
          "Content-Type": "multipart/form-data"
        })
        .then(res => {
          this.url = res.data.data.url;
          console.log(this.url);
          this.success = "1";
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //删除按钮
    remove() {
      this.$confirm("确定要删除图片么", "提示", {
        // type: 'warning'
      })
        .then(() => {
          this.width.width = "0px";
          this.fileList = [];
          this.url = "";
          this.success = "";
        })
        .catch(() => {});
    },
    //取消按钮
    cancel() {
      if (
        (this.ruleForm.name == "") &
        (this.ruleForm.types == "") &
        (this.ruleForm.desc == "") &
        (this.ruleForm.link == "") &
        (this.url == "")
      ) {
        this.$router.push({ path: "/table" });
      } else {
        this.$confirm("取消将不会保存编写内容，确定返回吗？", "提示", {
          // type: 'warning'
        })
          .then(() => {
            this.$router.push({ path: "/table" });
          })
          .catch(() => {});
      }
    }
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  }
};
</script>
<style>
.demo-ruleForm {
  padding: 20px 15%;
  background: #fff;
}
.el-main {
  line-height: normal;
}
.headers {
  border-bottom: 1px solid #ccc;
  text-align: left;
  padding: 25px 20px;
  background-color: #e9eef3;
  font-weight: 700;
}
.content {
  border: 1px solid #ccc;
  margin: 15px 25px;
  border-radius: 5px;
}
.el-select1 {
  float: left;
  margin-right: 10px;
}
.imge {
  width: 100%;
  height: 100%;
}
.clip {
  transition-delay: 1s;
  height: 0.375rem;
  background-color: cornflowerblue;
  margin-top: 15px;
}
.el-upload-list {
  display: none;
}
.ql-editor {
  height: 200px;
}
.ql-snow .ql-picker-label::before {
  position: absolute;
  right: 20%;
}
.Layout-list {
  display: flex;
  justify-content: space-around;
  margin-left: 6.7rem;
  border-bottom: 1px solid gainsboro;
  margin-right: 15%;
  padding-bottom: 10px;
}
</style>




