<template>
  <el-container>
    <el-aside width="250px">
      <el-row class="tac">
        <el-col :span="24">
          <h5>萝卜多后台管理系统</h5>
          <el-menu
            default-active="this.$route.path"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b"
            router
          >
            <el-submenu index="1">
              <template slot="title">
                <i class="el-icon-s-order"></i>
                <span>后台管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="#">选项1</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="2">
              <template slot="title">
                <i class="el-icon-message-solid"></i>
                <span>信息管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="##">选项2</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title">
                <i class="el-icon-edit"></i>
                <span>内容管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/Table">Article列表</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-col>
      </el-row>
    </el-aside>
    <el-container>
      <el-header>
        <div class="header">
          <span class="head">猎头</span>
          <span class="vertical">|</span>
          <span class="head">user</span>
          <span class="vertical">|</span>
          <i class="el-icon-switch-button" @click="quit" style="cursor:pointer"></i>
        </div>
      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>


<script>
import ArticleList from "./nav1/Table";
import axios from "axios";
export default {
  components: {
    ArticleList
  },
  methods: {
    //退出功能
    quit() {
      this.$http.post("carrots-admin-ajax/a/logout").then(res => {
        console.log(res);
        this.$confirm("确认退出吗?", "提示", {
          type: "warning"
        })
          .then(() => {
            this.$router.push({ path: "/login" });
          })
          .catch(() => {});
      });
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  }
};
</script>

<style>
.vertical {
  margin: 0 20px;
}
.head {
  color: #6699cc;
}
.header {
  float: right;
  padding-right: 10px;
}
html,
body {
  margin: auto;
}
.tac {
  height: -webkit-fill-available;
}
.el-header,
.el-footer {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: rgb(84, 92, 100);
  color: #333;
  text-align: center;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  line-height: 160px;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>


