<template>
  <section>
    <div class="main">
      <div class="date">
        <!-- 日期选择器 -->
        <div style="display:flex;align-items: center">
          <span class="demonstration">发布时间</span>
          <el-date-picker
            v-model="value1"
            type="date"
            placeholder="选择日期"
            :picker-options="pickerOptions"
          ></el-date-picker>
          <span class="splitter">—</span>
          <el-date-picker
            v-model="value2"
            align="right"
            type="date"
            placeholder="选择日期"
            :picker-options="pickerOptions"
          ></el-date-picker>
        </div>

        <!-- 类型筛选 -->
        <div style="padding-top: 10px;">
          <span class="demonstration">类&emsp;&emsp;型</span>
          <el-select v-model="value3" filterable placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
      </div>

      <!-- 状态筛选 -->
      <div class="date" style="justify-content: right;">
        <span class="demonstration">状&emsp;&emsp;态</span>
        <el-select v-model="value4" placeholder="请选择">
          <el-option
            v-for="state in status"
            :key="state.value"
            :label="state.label"
            :value="state.value"
          ></el-option>
        </el-select>
      </div>

      <!-- 搜索清空按钮 -->
      <div class="date" style="justify-content: flex-end;">
        <el-button
          type="primary"
          icon="el-icon-delete"
          @click="empty"
          style="background: #d9534f;border-color: #d43f3a;"
        >清空</el-button>
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="search"
          style="background: #5cb85c;border-color: #4cae4c"
        >搜索</el-button>
      </div>
    </div>

    <!-- article列表渲染 -->
    <el-table
      :data="arr.slice((currentPage-1)*pagesize,currentPage*pagesize)"
      style="width: 100%"
      ref="multipleTable"
      tooltip-effect="dark"
    >
      <div class="toolbar">
        <span>articlel列表</span>
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="newfrom"
          style="background: #5cb85c;border-color: #4cae4c;padding: 8px 6px;"
        >新增</el-button>
      </div>
      <el-table-column type="index" label="ID"></el-table-column>
      <el-table-column prop="title" label="名称"></el-table-column>
      <el-table-column prop="type" label="类型" :formatter="formatType"></el-table-column>
      <el-table-column prop="createAt" label="发布时间" :formatter="formatCreateAt"></el-table-column>
      <el-table-column prop="updateAt" label="修改时间" :formatter="formatUpdateAt"></el-table-column>
      <el-table-column prop="author" label="发布者"></el-table-column>
      <el-table-column prop="status" label="状态" :formatter="formatStatus"></el-table-column>
      <el-table-column prop="status" label="操作">
        <template slot-scope="scope">
          <el-button
            @click="handleClick(scope.row)"
            type="text"
            size="small"
            class="statuss"
            v-if="scope.row.status === 2"
          >下线</el-button>
          <el-button
            @click="handleClick(scope.row)"
            type="text"
            size="small"
            class="statuss"
            v-if="scope.row.status === 1"
          >上线</el-button>
          <el-button type="text" size="small" @click.native="edit(scope.$index,scope.row)">编辑</el-button>
          <el-button type="text" size="small" @click="remove(scope.$index,scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      class="Pagination"
      @size-change="handleSizeChange"
      @current-change="current_change"
      :current-page="currentPage"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      :page-sizes="[5, 10, 15, 20]"
    ></el-pagination>
  </section>
</template>

  <script>
import "element-ui/lib/theme-chalk/display.css";
import moment from "moment";
import axios from "axios";
import qs from "qs";
export default {
  name: "ArticleList",
  //此方法进入页面立即执行
  created() {
    this.getarticle();
  },
  data() {
    return {
      total: 2,
      pagesize: 10,
      currentPage: 1,
      arr: [],
      //类型选择
      options: [
        {
          value: "",
          label: "全部"
        },
        {
          value: "0",
          label: "首页banner"
        },
        {
          value: "1",
          label: "找职位banner"
        },
        {
          value: "2",
          label: "找精英banner"
        },
        {
          value: "3",
          label: "行业大图"
        }
      ],
      value3: "",
      //状态选择
      status: [
        {
          value: "",
          label: "全部"
        },
        {
          value: "2",
          label: "上线"
        },
        {
          value: "1",
          label: "草稿"
        }
      ],
      value4: "",
      //日期选择
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      value1: "",
      value2: ""
    };
  },
  methods: {
    handleSizeChange(val) {
      console.log(val);
      this.pagesize = val;
    },
    //上下线功能
    handleClick(row) {
      let _this = this;
      let data = {
        id: row.id,
        status: row.status
      };
      let newdata = qs.stringify(data);
      console.log(row.status);
      //判断上下线进行提示
      if (row.status == 1) {
        this.$confirm(
          "上线后该图片将在轮播banner中展示，是否执行上线操作？",
          "操作提示",
          {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }
        )
          .then(() => {
            axios
              .put("carrots-admin-ajax/a/u/article/status", newdata)
              .then(function(res) {
                if (row.status == 1) {
                  row.status = 2;
                } else {
                  row.status = 1;
                }
                console.log(res);
              });
            this.$message({
              type: "success",
              message: "上线成功!"
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消上线"
            });
          });
      } else {
        this.$confirm(
          "下线后该图片将不展示在轮播banner中，是否执行下线操作？",
          "操作提示",
          {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }
        )
          .then(() => {
            axios
              .put("carrots-admin-ajax/a/u/article/status", newdata)
              .then(function(res) {
                if (row.status == 1) {
                  row.status = 2;
                } else {
                  row.status = 1;
                }
                console.log(res);
              });
            this.$message({
              type: "success",
              message: "下线成功!"
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消下线"
            });
          });
      }
    },
    current_change: function(currentPage) {
      this.currentPage = currentPage;
    },
    mounted: function() {
      this.addUser();
    },

    //编辑按钮
    edit(index, row) {
      let id = row.id;
      console.log(id);
      localStorage.setItem("EditID", id);
      this.$router.push({
        name: "Form",
        params: {
          type: 1
        }
      });
    },
    //新增按钮
    newfrom() {
      this.$router.push({ path: "/form" });
    },
    //清空按钮
    empty() {
      let _this = this;
      _this.value1 = "";
      _this.value2 = "";
      _this.value3 = "";
      _this.value4 = "";
      axios
        .get("carrots-admin-ajax/a/article/search", {
          params: {
            size: 999
          }
        })
        .then(function(lst) {
          console.log(lst); //请求正确时执行的代码
          _this.arr = lst.data.data.articleList;
          _this.total = lst.data.data.total;
          console.log(_this.arr);
        })
        .catch(function(response) {
          console.log(response); //发生错误时执行的代码
        });
    },
    // 类型转换
    formatType: function(row, column) {
      return row.type == "0"
        ? "首页banner"
        : row.type == "1"
        ? "找职位banner"
        : row.type == "2"
        ? "找精英banner"
        : row.type == "3"
        ? "行业大图"
        : "暂无";
    },
    // 时间戳转换
    formatCreateAt: function(row, column) {
      let date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return moment(date).format("YYYY-MM-DD HH:mm:ss");
    },
    // 时间戳转换
    formatUpdateAt: function(row, column) {
      let date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return moment(date).format("YYYY-MM-DD HH:mm:ss");
    },
    // 状态转换
    formatStatus: function(row, column) {
      return row.status == "1" ? "草稿" : row.status == "2" ? "上线" : "草稿";
    },
    formatStatus1: function(row, column) {
      return row.status == "2" ? "上线" : row.status == "1" ? "下线" : "草稿";
    },
    // 请求article列表
    getarticle() {
      var statuss = document.getElementsByClassName("statuss");
      let _this = this;
      axios
        .get("carrots-admin-ajax/a/article/search", {
          params: {
            size: 999
          }
        })
        .then(function(lst) {
          console.log(lst); //请求正确时执行的代码
          _this.arr = lst.data.data.articleList;
          _this.total = lst.data.data.total;
          console.log(_this.arr);
        })
        .catch(function(response) {
          console.log(response); //发生错误时执行的代码
        });
    },
    //搜索按钮
    search() {
      let _this = this;
      console.log(this.value1, this.value2);
      var date1 = new Date(this.value1);
      var date2 = new Date(this.value2).setHours(23, 59, 59, 999);
      let value1 = date1.valueOf();
      let value2 = date2.valueOf();
      console.log(value1, value2);
      if (value1 !== value1 || value2 !== value2) {
        axios
          .get("carrots-admin-ajax/a/article/search", {
            params: {
              type: this.value3,
              status: this.value4,
              size: 999
            }
          })
          .then(function(articles) {
            console.log(articles);
            _this.arr = articles.data.data.articleList;
            _this.total = articles.data.data.total;
          })
          .catch(function(err) {
            console.log(err);
          });
      } else if (value1 == 0 || value2 == 0) {
        axios
          .get("carrots-admin-ajax/a/article/search", {
            params: {
              type: this.value3,
              status: this.value4,
              size: 999
            }
          })
          .then(function(articles) {
            console.log(articles);
            _this.arr = articles.data.data.articleList;
            _this.total = articles.data.data.total;
          })
          .catch(function(err) {
            console.log(err);
          });
      } else {
        axios
          .get("carrots-admin-ajax/a/article/search", {
            params: {
              type: this.value3,
              status: this.value4,
              startAt: value1,
              endAt: value2,
              size: 999
            }
          })
          .then(function(articles) {
            console.log(articles);
            _this.arr = articles.data.data.articleList;
            _this.total = articles.data.data.total;
          })
          .catch(function(err) {
            console.log(err);
          });
      }
    },
    //删除按钮
    remove(index, row) {
      let _this = this;
      let id = row.id;
      console.log(id);
      this.$http.delete("carrots-admin-ajax/a/u/article/" + id).then(res => {
        this.$confirm("确认删除吗？", "提示", {
          type: "warning"
        })
          .then(() => {
            _this.getarticle();
          })
          .catch(() => {});
      });
    }
  }
};
</script>
  <style>
.Pagination {
  float: right;
  margin: 10px 10px 0 0;
}
.toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 10px;
  font-size: large;
  border-bottom: 1px solid #e5e9f2;
  visibility: visible;
}
.el-table .hidden-columns {
  position: static;
}
.main {
  background-color: #fff;
  margin-bottom: 10px;
  padding: 0 10px;
}
.splitter {
  margin: 0 50px;
}
.demonstration {
  margin: 0 50px 0 0;
}
.date {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0px;
  flex-wrap: wrap;
}
.el-main {
  line-height: 0px;
}
.el-row {
  margin-bottom: 20px;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
</style>
  


